import wPicker from "@/components/w-picker/w-picker.vue";
export default {
	data() {
		return {
			array: ['1杯', '2杯', '3杯', '4杯'],
			index: 0,
			selectList: [{
				label: '1杯',
				value: '1'
			}, {
				label: '2杯',
				value: '2'
			}, {
				label: '3杯',
				value: '3'
			}, {
				label: '4杯',
				value: '4'
			}],
			themeColor: '#a69eff',
			water: '饮水量',
			urinate: '排尿量',
			surplus: '残留量',
			waterValue: '',
			urinateValue: '',
			surplusValue: '',
			waterRecord: false,
			urinateRecord: false,
			surplusRecord: false,
			hasWaterRecord: false,
			hasUrinateRecord: false,
			hasSurplusRecord: false,
			currentTime: '',
			inputSurplus: '',
			isHide: false
		}
	},
	methods: {
		openOne() {
			this.$refs.selectorOne.show();
		},
		openTwo() {
			this.$refs.selectorTwo.show();
		},
		openThree() {
			this.isHide = true;
		},
		onConfirmWater(e) {
			this.waterValue = e.result;
			const date = new Date();
			let Hours, Minutes = '';
			if (date.getHours().toString().length < 2) {
				Hours = '0' + date.getHours();
			} else {
				Hours = date.getHours();
			}
			if (date.getMinutes().toString().length < 2) {
				Minutes = '0' + date.getMinutes();
			} else {
				Minutes = date.getMinutes();
			}
			this.currentTime = Hours + ':' + Minutes;
			if (this.waterValue != '') {
				this.hasWaterRecord = true;
				this.waterRecord = true;
			} else {
				this.hasWaterRecord = false;
			}
		},
		onConfirmUrinate(e) {
			this.urinateValue = e.result;
			const date = new Date();
			let Hours, Minutes = '';
			if (date.getHours().toString().length < 2) {
				Hours = '0' + date.getHours();
			} else {
				Hours = date.getHours();
			}
			if (date.getMinutes().toString().length < 2) {
				Minutes = '0' + date.getMinutes();
			} else {
				Minutes = date.getMinutes();
			}
			this.currentTime = Hours + ':' + Minutes;
			if (this.urinateValue != '') {
				this.hasUrinateRecord = true;
				this.urinateRecord = true;
			} else {
				this.hasUrinateRecord = false;
			}
		},
		onConfirmSurplus(e) {
			this.surplusValue = this.inputSurplus;
			const date = new Date();
			let Hours, Minutes = '';
			if (date.getHours().toString().length < 2) {
				Hours = '0' + date.getHours();
			} else {
				Hours = date.getHours();
			}
			if (date.getMinutes().toString().length < 2) {
				Minutes = '0' + date.getMinutes();
			} else {
				Minutes = date.getMinutes();
			}
			this.currentTime = Hours + ':' + Minutes;
			if (this.surplusValue != '') {
				this.hasSurplusRecord = true;
				this.surplusRecord = true;
			} else {
				this.hasSurplusRecord = false;
			}
			this.isHide = false;	
		},
		showWaterBox() {
			if (this.hasWaterRecord == true) {
				this.waterRecord = !this.waterRecord;
			}
		},
		showUrinateBox() {
			if (this.hasUrinateRecord == true) {
				this.urinateRecord = !this.urinateRecord;
			}
		},
		showSurplusBox() {
			if (this.hasSurplusRecord == true) {
				this.surplusRecord = !this.surplusRecord;
			}
		},
		surplusInput(e) {
			this.inputSurplus = e.detail.value;
		},
		closeModel() {
			this.isHide = false;
		},
		confirm() {
			uni.navigateBack({
				delta: 1
			});
		}
	},
	components: {
		wPicker
	}
}
